// Імпорт необхідних модулів
const express = require('express'); // Основний фреймворк для роботи з HTTP
const bodyParser = require('body-parser'); // Модуль для парсингу тіла запиту
const path = require('path'); // Модуль для роботи з файловими шляхами
const session = require('express-session'); // Модуль для управління сесіями
const app = express(); // Створення екземпляра додатку Express

// Імпорт маршрутів
const indexRoutes = require('./routes/index'); // Маршрути для головної сторінки
const adminRoutes = require('./routes/admin'); // Маршрути для адмін-панелі
const authRoutes = require('./routes/auth');   // Маршрути для аутентифікації
const bookRoutes = require('./routes/book');   // Маршрути для роботи з книгами

// Налаштування движка шаблонів
app.set('view engine', 'ejs'); // Використання EJS як движка шаблонів

// Налаштування парсингу тіла запиту
app.use(bodyParser.urlencoded({ extended: true })); // Дозволяє парсити URL-кодовані тіла запитів

// Налаштування статичних файлів
app.use(express.static(path.join(__dirname, 'public'))); // Додає статичні файли з папки 'public'

// Налаштування сесій
app.use(session({
    secret: 'your-secret-key', // Ключ для шифрування сесії
    resave: false, // Не зберігати сесію, якщо вона не була змінена
    saveUninitialized: true // Зберігати нові сесії, навіть якщо вони не були ініціалізовані
}));

// Налаштування маршрутів
app.use('/', indexRoutes); // Головні маршрути
app.use('/admin', adminRoutes); // Маршрути адмін-панелі
app.use('/auth', authRoutes);   // Маршрути аутентифікації
app.use('/books', bookRoutes);   // Маршрути для книг

// Запуск сервера
const PORT = 8080; // Порт, на якому запуститься сервер
app.listen(PORT, () => {
    console.log(`Server listening at http://localhost:${PORT}`); // Лог для підтвердження запуску сервера
});
